:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --dark-blue: hsl(240, 38%, 20%);
  --grayish-blue: hsl(240, 18%, 77%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.bg-pattern {
  background-image: url("/images/pattern-bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  height: 50vh;
  margin: 0.625rem auto;
  padding: 1.5625rem;
}

.image-container {
  overflow: hidden;
  width: 13.4375rem;
  height: 13.4375rem;
  border-radius: 0.5rem;
  margin: 0 auto;
}
.image-container img {
  width: auto;
  height: 100%;
}

.profile {
  position: relative;
}

.nav-buttons {
  position: absolute;
  bottom: -1.25rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 0.625rem;
  border-radius: 1.875rem;
  box-shadow: 3px 10px 25px 0px rgba(0, 0, 0, 0.3);
  width: 5rem;
}
.nav-buttons img {
  width: 0.9375rem;
  height: 0.9375rem;
}

.slide {
  padding: 1.25rem;
}